import React, { Component } from "react";
import PropTypes from "prop-types";
import { compose } from "redux";
import { connect } from "react-redux";
import { firebaseConnect } from "react-redux-firebase";
import { Redirect, Link } from "react-router-dom";
import { notifyUser } from "../../actions/notifyAction";
import Alert from "../layout/Alert";

class Login extends Component {
  state = {
    email: "",
    password: "",
    isLoggedIn: false
  };
  // event handler to handle submit
  handleSubmit = e => {
    e.preventDefault();

    const { firebase, notifyUser } = this.props;
    const { email, password } = this.state;

    firebase
      .login({
        email,
        password
      })
      .then(() => this.setState({ isLoggedIn: true }))
      .catch(err => notifyUser("Invalid Login credentials", "error"));
  };
  // event handler to handle change
  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };
  // life cycle method
  render() {
    const { message, messageType } = this.props.notify;
    const { isLoggedIn } = this.state;

    if (isLoggedIn) {
      return <Redirect to="/dashboard" />;
    }
    return (
      <div className="centre">
        <div className="container mb-5">
          <div className="container">
            <div className="row">
              <div className="col-sm-9 col-md-7 col-lg-5 mx-auto">
                <p className="h8 mb-0 mt-4 font-weight-bold text-center text-black">
                  Hey Boss, Welcome Back!
                </p>
                <div className="my-5">
                  <div className="">
                    {message ? (
                      <Alert message={message} messageType={messageType} />
                    ) : null}
                    <p className="h3 text-center mt-2">
                      <b>Polixe</b>
                    </p>
                    <form onSubmit={this.handleSubmit}>
                      <div className="col-auto">
                        <label className="sr-only" htmlFor="inlineFormInputGroup">
                          Enter your phone number
                        </label>
                        {/* Username */}
                        <div className="input-group mb-2">
                          <div className="input-group-prepend">
                            <div className="input-group-text">
                              <i className="fa fa-envelope" />
                            </div>
                          </div>
                          <input
                            type="email"
                            className="form-control"
                            name="email"
                            onChange={this.handleChange}
                            value={this.state.email}
                            placeholder="Email"
                          />
                        </div>
                        {/* Email */}
                        <div className="input-group mb-2">
                          <div className="input-group-prepend">
                            <div className="input-group-text">
                              <i className="fa fa-lock" />
                            </div>
                          </div>
                          <input
                            type="password"
                            className="form-control"
                            name="password"
                            onChange={this.handleChange}
                            value={this.state.password}
                            placeholder="Password"
                          />
                        </div>
                      </div>
                      <button
                        className="btn orange-gradient shadow btn-block"
                        type="submit"
                      >
                        SIGN IN <i className="fa fa-send" />
                      </button>
                    </form>
                    <small className="mt-3 text-muted">
                      Don't have an account? <Link to="/register">Sign up</Link>
                    </small>
                    <div className=" mt-4">
                      <small className=" text-muted text-center mt-3">
                        <a href="/register">Forgot password?</a>
                      </small>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
// default prop type
Login.propTypes = {
  firebase: PropTypes.object.isRequired
};

export default compose(
  firebaseConnect(),
  connect(
    (state, props) => ({
      notify: state.notify
    }),
    { notifyUser }
  )
)(Login);
