import React, { Component } from "react";
import Fade from "react-reveal/Fade";
import { Link } from "react-router-dom";
import Footer from "../layout/Footer";
import Header from "../layout/Header";

import bitcoin_img from "../../images/bitcoin.jpg";
import giftcard_img from "../../images/gift-cards.png";
import airtime_img from "../../images/airtime.jpeg";
import a2c_img from "../../images/a2c.jpeg";
import paybills_img from "../../images/pay_bills.jpeg";


class Home extends Component {
  //life cycle method
  constructor() {
    super();
    this.state = {};
  }

  // life cycle method
  render() {
    return (
      <div className="">
        <Header />
        {/* jumbotron section */}
        <Fade left>
          <div
            className="jumbotron jumbotron-fluid header"
            style={{ height: "600px" }}
          >
            <div className="container mt-5 text-left">
              <p className="mt-5 text-justify">Welcome to Polixe</p>
              <p className="h1 font-weight-lighter mb-3">
                Make <b className="text-warning font-weight-bold">Easy</b>{" "}
                Payments
              </p>
              <Link
                to="/login"
                className="mb-3 btn btn-outline-warning shadow pl-5 pr-5 shadow p-3 font-weight-bold mr-3"
              >
                GET STARTED
              </Link>
              <Link
                to="/login"
                className="btn orange-gradient shadow pl-5 pr-5 shadow p-3 font-weight-bold mr-3"
              >
                ABOUT US
              </Link>
            </div>
          </div>
        </Fade>

        {/* Timer Section */}
        <Fade right>
          <div className="container text-center mt-5 mb-5">
            <h3 className=" font-weight-bold">
              Trusted By Over 1000+ Nigerians
            </h3>
            <p className="">
              Buy Prime is trusted by many nigerians in handling online
              transations
            </p>
          </div>
        </Fade>

        {/* About Section */}
        <Fade bottom>
          <div className="orange-gradient p-5">
            <div className="container mb-5">
              <div className="row mb-5">
                <div className="col-sm">
                  <img
                    src="https://assets.paystack.com/assets/img/content/masonry/_768x512_crop_center-center_60_line/171021PSW119-compressor.jpg"
                    alt="buyprime event"
                    className="img-fluid thumbnail rounded-lg"
                  />
                </div>
                <div className="col-sm">
                  <h3 className="">Who are we?</h3>
                  <p>
                    BuyPrime , owned by Pxpress Enterprise Nigeria, is a
                    Nigerian owned business whose aim is to bring you an
                    effortless way to pay bills and help you out when you are in
                    need of urgent online transactions.
                  </p>
                </div>
              </div>
              <div className="row">
                <div className="col-sm">
                  <h3 className="">What we do?</h3>
                  <p>
                    BuyPrime , owned by Pxpress Enterprise Nigeria, offers
                    speedy and best deals for your internet data plans,airtime
                    to cash conversion all bill payments like EEDC, Gotv, DSTV
                    and Waec scratch pins
                  </p>
                </div>
                <div className="col-sm">
                  <img
                    src="https://assets.paystack.com/assets/img/content/masonry/_768x512_crop_center-center_60_line/171021PSW119-compressor.jpg"
                    alt="buyprime event"
                    className="img-fluid thumbnail rounded-lg"
                  />
                </div>
              </div>
            </div>
          </div>
        </Fade>

        {/* Products Section */}
        <h3 className="text-center mt-5 mb-5 text-muted">OUR PRODUCTS</h3>
        <div className="container">
          <div className="row text-center mb-3">
            <div className="col-sm">
              <div className="card border-0 shadow">
                <img src={airtime_img} class="card-img-top" alt="..." />
                <div className="card-body">
                  <h3>Data plans</h3>
                  <p>
                    Buy data plans for all service providers in Nigeria without
                    stress. Did I forget to mention that we have the best rates
                    available!
                  </p>
                  <Link to="/login" className="btn yellow-gradient">
                    Buy now
                  </Link>
                </div>
              </div>
            </div>
            <div className="col-sm">
              <div className="card border-0 shadow">
                <img src={airtime_img} class="card-img-top" alt="..." />
                <div className="card-body">
                  <h3>Airtime VTU</h3>
                  <p>
                    Buy data plans for all service providers in Nigeria without
                    stress. Did I forget to mention that we have the best rates
                    available!
                  </p>
                  <Link to="/login" className="btn yellow-gradient">
                    Start now
                  </Link>
                </div>
              </div>
            </div>
            <div className="col-sm">
              <div className="card border-0 shadow">
                <img src={paybills_img} class="card-img-top" alt="..." />
                <div className="card-body">
                  <h3>Bills payment</h3>
                  <p>
                    Want to pay for bills but don't have the time? No worries,
                    BuyPrime handles that for you. With few steps you'll be up
                    and running
                  </p>
                  <Link to="/login" className="btn yellow-gradient">
                    Start now
                  </Link>
                </div>
              </div>
            </div>
          </div>
          <div className="row text-center mt-3">
            <div className="col-sm">
              <div className="card border-0 shadow">
                <img src={a2c_img} class="card-img-top" alt="..." />
                <div className="card-body">
                  <h3>Airtime2cash</h3>
                  <p>
                    Out of cash? BuyPrime got you covered. Convert your airtime
                    to cash at the best rates
                  </p>
                  <Link to="/login" className="btn yellow-gradient">
                    Convert now
                  </Link>
                </div>
              </div>
            </div>
            <div className="col-sm">
              <div className="card border-0 shadow">
                <img src={bitcoin_img} class="card-img-top" alt="..." />
                <div className="card-body">
                  <h3>Bitcoin</h3>
                  <p>
                    Want to buy or sell bitcoin? we have incredible rates
                    that'll stun you.
                  </p>
                  <Link to="/login" className="btn yellow-gradient">
                    Buy/Sell now
                  </Link>
                </div>
              </div>
            </div>
            <div className="col-sm">
              <div className="card border-0 shadow">
                <img src={giftcard_img} class="card-img-top" alt="..." />
                <div className="card-body">
                  <h3>Gift cards</h3>
                  <p>
                    No matter the gift card, we've got you covered. Why not
                    check our store and purchase now.
                  </p>
                  <Link to="/login" className="btn yellow-gradient">
                    Buy/Sell now
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Fequently asked questions */}
        <div className="accordion container mt-5" id="accordionExample">
          <h3 className="text-center mt-5 mb-5 text-muted">
            FREQUENTLY ASKED QUESTIONS
          </h3>
          <div className="card">
            <div className="card-header" id="headingOne">
              <h2 className="mb-0">
                <button
                  className="btn btn-link text-dark"
                  type="button"
                  data-toggle="collapse"
                  data-target="#collapseOne"
                  aria-expanded="false"
                  aria-controls="collapseOne"
                >
                  When will i recieve the cash?
                </button>
              </h2>
            </div>

            <div
              id="collapseOne"
              className="collapse show"
              aria-labelledby="headingOne"
              data-parent="#accordionExample"
            >
              <div className="card-body">
                After a successful transaction, you'll recieve your cash within
                5 MINUTES
              </div>
            </div>
          </div>
          <div className="card">
            <div className="card-header" id="headingTwo">
              <h2 className="mb-0">
                <button
                  className="btn btn-link collapsed text-dark"
                  type="button"
                  data-toggle="collapse"
                  data-target="#collapseTwo"
                  aria-expanded="false"
                  aria-controls="collapseTwo"
                >
                  What are your fees
                </button>
              </h2>
            </div>
            <div
              id="collapseTwo"
              className="collapse"
              aria-labelledby="headingTwo"
              data-parent="#accordionExample"
            >
              <div className="card-body">
                Our fees ranges depending on the service. But we charge 10%
              </div>
            </div>
          </div>
          <div className="card">
            <div className="card-header" id="headingThree">
              <h2 className="mb-0">
                <button
                  className="btn btn-link collapsed text-dark"
                  type="button"
                  data-toggle="collapse"
                  data-target="#collapseThree"
                  aria-expanded="false"
                  aria-controls="collapseThree"
                >
                  How do i convert airtime to cash?
                </button>
              </h2>
            </div>
            <div
              id="collapseThree"
              className="collapse"
              aria-labelledby="headingThree"
              data-parent="#accordionExample"
            >
              <div className="card-body">
                <p>
                  Follow these simple steps <br />
                  -------------------------- <br />
                  1. Create an account
                  <br />
                  2. Login to your dashboard
                  <br />
                  3. Once logged in, you'll see a CONVERT Airtime2cash button.{" "}
                  <br />
                  And that's it
                </p>
              </div>
            </div>
          </div>
        </div>

        {/* Contact us section */}
        <div className="container contact-form mt-5">
          <div className="contact-image">
            <img
              src="https://image.ibb.co/kUagtU/rocket_contact.png"
              alt="rocket_contact"
            />
          </div>
          <form method="">
            <h3>Drop Us a Message</h3>
            <div className="row">
              <div className="col-lg-12">
                <div className="form-group">
                  <input
                    type="text"
                    name="txtName"
                    className="form-control"
                    placeholder="Your Name *"
                  />
                </div>
                <div className="form-group">
                  <input
                    type="text"
                    name="txtEmail"
                    className="form-control"
                    placeholder="Your Email *"
                  />
                </div>
                <div className="form-group">
                  <input
                    type="text"
                    name="txtPhone"
                    className="form-control"
                    placeholder="Your Phone Number *"
                  />
                </div>
                <div className="form-group">
                  <textarea
                    name="txtMsg"
                    className="form-control"
                    placeholder="Your Message *"
                    style={{ width: "100%", height: "150px" }}
                  ></textarea>
                </div>
                <div className="form-group">
                  <input
                    type="submit"
                    name="btnSubmit"
                    className="btnContact btn btn-warning"
                    value="Send Message"
                  />
                </div>
              </div>
              {/* <div class="col-md-6"></div> */}
            </div>
          </form>
        </div>
        <Fade bottom>
          <Footer />
        </Fade>
      </div>
    );
  }
}

// const logoStyle = {
//     color: 'orangeRed',
//     fontWeight: 'bolder'
// }

export default Home;
