import React, { Component } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { compose } from "redux";
import { connect } from "react-redux";
import { firebaseConnect } from "react-redux-firebase";
import { notifyUser } from "../../actions/notifyAction";
import Alert from "../layout/Alert";
import { Redirect } from "react-router-dom";

const nodeMailer = require("nodemailer");

class Register extends Component {
  state = {
    username: "",
    email: "",
    password: "",
    confirm_password: "",
    isRegistered: false
  };

  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  handleSubmit = e => {
    e.preventDefault();

    const { firebase, notifyUser, auth } = this.props;
    const { email, password, username } = this.state;

    firebase
      .createUser({
        email: email,
        emailVerified: false,
        phoneNumber: "+11234567890",
        password: password,
        displayName: { username },
        photoURL: "http://www.example.com/12345678/photo.png",
        disabled: false
      })
      .then(console.log("User created!"))
      .catch(err => notifyUser("User already exists", "error"));

    // // Mail sending code
    // let transporter = nodeMailer.createTransport({
    //   host: "smtp.gmail.com",
    //   port: 465,
    //   secure: true,
    //   auth: {
    //     user: "hamsaharcourt.com",
    //     pass: "eckankar2757101"
    //   }
    // });

    // const mailOptions = {
    //   from: '"Polixe" <support@polixe.com>', // sender address
    //   to: { email }, // list of receivers
    //   subject: "Welcome to Polixe", // Subject line
    //   text: "Thank you for signing in to Polixe", // plain text body
    //   html: "<b>Welcome to Polixe</b>" // html body
    // };

    // transporter.sendMail(mailOptions, (error, info) => {
    //   if (error) {
    //     return console.log(error, " LOOK AT MEEEEEEEEEEEEE");
    //   }
    //   console.log("Message %s sent: %s", info.messageId, info.response);
    // });
  };

  render() {
    const { isRegistered } = this.state;
    const { message, messageType } = this.props.notify;

    // if (isRegistered) {
    //   return <Redirect to="/login" />;
    // }
    return (
      <div className="col-sm-9 col-md-7 col-lg-5 mx-auto">
        <div className="container">
          <div className="row">
            <div className="col-lg-10 col-xl-9 mx-auto my-auto">
              <p className="h5 mb-5  mt-5 text-center">
                Hi Boss, It's nice to meet you!
              </p>
              <div className="flex-row my-5 ">
                <div className="">
                  {message ? (
                    <Alert message={message} messageType={messageType} />
                  ) : null}
                  <h2 className="h2 text-center mt-2">Polixe</h2>
                  <form onSubmit={this.handleSubmit}>
                    <div className="col-auto">
                      <label className="sr-only" htmlFor="inlineFormInputGroup">
                        Enter your phone number
                      </label>
                      {/* Username */}
                      <div className="input-group mb-2">
                        <div className="input-group-prepend">
                          <div className="input-group-text">@</div>
                        </div>
                        <input
                          type="text"
                          className="form-control"
                          placeholder="Username"
                          name="username"
                          value={this.state.username}
                          onChange={this.handleChange}
                          required
                        />
                      </div>
                      {/* Email */}
                      <div className="input-group mb-2">
                        <div className="input-group-prepend">
                          <div className="input-group-text">
                            <i className="fa fa-envelope" />
                          </div>
                        </div>
                        <input
                          type="email"
                          placeholder="Email"
                          className="form-control"
                          name="email"
                          value={this.state.email}
                          onChange={this.handleChange}
                          required
                        />
                      </div>
                      {/* Password */}
                      <div className="input-group mb-2">
                        <div className="input-group-prepend">
                          <div className="input-group-text">
                            <i className="fa fa-lock" />
                          </div>
                        </div>
                        <input
                          type="password"
                          className="form-control"
                          placeholder="Password"
                          name="password"
                          value={this.state.password}
                          onChange={this.handleChange}
                          required
                        />
                      </div>
                      {/* Confirm password */}
                      <div className="input-group mb-2">
                        <div className="input-group-prepend">
                          <div className="input-group-text">
                            <i className="fa fa-lock" />
                          </div>
                        </div>
                        <input
                          type="password"
                          className="form-control"
                          placeholder="Confirm password"
                          name="confirm_password"
                          value={this.state.confirm_password}
                          onChange={this.handleChange}
                          required
                        />
                      </div>

                      <button
                        className="btn orange-gradient shadow btn-block"
                        type="submit"
                      >
                        SIGN UP <i className="fa fa-send" />
                      </button>
                    </div>
                  </form>
                  <small className="mt-3 text-muted">
                    Already have an account? <Link to="/login">Sign in</Link>
                  </small>

                  <p className="mb-2 mt-2 text-muted text-center">OR</p>

                  <button
                    className="shadow btn btn-primary shadow btn-block"
                    type="submit"
                  >
                    Continue with Facebook
                  </button>
                  <button
                    className="shadow btn btn-danger shadow btn-block"
                    type="submit"
                  >
                    Continue with Google
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

// default prop type
Register.propTypes = {
  firebase: PropTypes.object.isRequired
};

export default compose(
  firebaseConnect(),
  connect(
    (state, props) => ({
      notify: state.notify,
      auth: state.firebase.auth
    }),
    { notifyUser }
  )
)(Register);
