import { createStore, combineReducers } from "redux";
import { firebaseReducer } from "react-redux-firebase";
import firebase from "firebase";
import "firebase/firestore"; // <- needed if using firestore
import { createFirestoreInstance, firestoreReducer } from "redux-firestore"; // <- needed if using firestore

//reducer
import notifyReducer from "./reducers/notifyReducer";

// Your web app's Firebase configuration
var firebaseConfig = {
  apiKey: "AIzaSyBZ9aoY9AvK38Tb3PYw14-6v_csCCaDDdU",
  authDomain: "polixe-d1a5e.firebaseapp.com",
  databaseURL: "https://polixe-d1a5e.firebaseio.com",
  projectId: "polixe-d1a5e",
  storageBucket: "polixe-d1a5e.appspot.com",
  messagingSenderId: "980962583893",
  appId: "1:980962583893:web:5e726a42f65476cb0cbbdc",
  measurementId: "G-LMG961T6FP"
};

// react-redux-firebase config
const rrfConfig = {
  userProfile: "users",
  useFirestoreForProfile: true // Firestore for Profile instead of Realtime DB
};

// Initialize firebase instance
firebase.initializeApp(firebaseConfig);

// Initialize other services on firebase instance
firebase.firestore(); // <- needed if using firestore

// Add firebase to reducers
const rootReducer = combineReducers({
  firebase: firebaseReducer,
  firestore: firestoreReducer, // <- needed if using firestore
  notify: notifyReducer
});

// Create store with reducers and initial state
const initialState = {};
const store = createStore(
  rootReducer,
  initialState,
  window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
);

export const rrfProps = {
  firebase,
  config: rrfConfig,
  dispatch: store.dispatch,
  createFirestoreInstance // <- needed if using firestore
};

export default store;
